export default{
	name:"videocontent",
	props:["current"],
	data() {
		return {
			myurl:"",
			fullurl:"",
			params:{
					limit:18,
					skip:0,
					order:"hot"					
			},
			videolist:[],
			hasmore:true
			
		}
	},
	watch:{
		current:function() {
			this.videolist = [];
			this.videocontent();
		}
	},
	methods:{
		videocontent() {
			if(this.current == 0){
				this.myurl ="/videoimg/v1/videowp/featured";
				this.fullurl = "http://157.122.54.189:9088/videoimg/v1/videowp/featured";

			}else if(this.current == 1){
				this.myurl ="/videoimg/v1/videowp/category/59b25abbe7bce76bc834198a";
				this.fullurl = "http://157.122.54.189:9088/videoimg/v1/videowp/category/59b25abbe7bce76bc834198a";
				this.params.order="new";
				
			}else if(this.current == 2) {
				this.myurl ="/videoimg/v1/videowp/videowp";
				this.fullurl = "http://157.122.54.189:9088/videoimg/v1/videowp/videowp";
				this.params.order="new";						
			}else if(this.current == 3) {
				this.myurl ="/videoimg/v1/videowp/videowp";
				this.fullurl = "http://157.122.54.189:9088/videoimg/v1/videowp/videowp";					
			}
			uni.showLoading({
				title:"数据加载中"
			})
			uni.request({
				



				

				url:this.fullurl,

									
				
				data:this.params,
				success: (result) => {
			//console.log(this.current)
					if(result.data.res.videowp.length == 0){
						this.hasmore =false;
						uni.showToast({
							title:"数据没有了",
							icon:"none"
						})
						return ;
					}
					this.videolist = [...this.videolist,...result.data.res.videowp];
					//console.log(this.videolist);
				},
				complete: () => {
					uni.hideLoading()
				}
			})	
		},
		scrolltolower() {
			if(this.hasmore){
				this.params.skip += this.params.limit;		
				this.videocontent();					
			}else{
				uni.showToast({
					title:"数据没有了",
					icon:"none"
				})					
			}

		},
		tovideodetails(item) {
			getApp().globalData.video = item;
			uni.navigateTo({
				url:"videoDetails/videoDetails",
			})
		}
	},
	mounted() {
		this.videocontent()
	}
}
